import React from "react";
import { Row, Col } from "react-bootstrap";
import Multiselect from "multiselect-react-dropdown";
import avatar from "../../images/avatar.png";
import add from "../../images/add.png";
import style from "./AdminAddProducts.module.css";

const AdminAddProducts = () => {
  const onSelect = () => {};
  const onRemove = () => {};

  const options = [
    { name: " ", id: 1 },
    { name: " ", id: 2 },
  ];

  return (
    <div>
      <Row className="justify-content-start ">
        <div className={`${style["admin-content-text"]} pb-4`}>   </div>
        <Col sm="8">
          <div className={`${style["text-form"]} pb-2`}>  </div>
          <img src={avatar} alt="" height="100px" width="120px" />
          <input type="text" className={`${style["input-form"]} d-block mt-3 px-3`} placeholder=" " />
          <textarea className={`${style["input-form-area"]} p-2 mt-3`} rows="4" cols="50" placeholder=" " />
          <input type="number" className={`${style["input-form"]} d-block mt-3 px-3`} placeholder="  " />
          <input type="number" className={`${style["input-form"]} d-block mt-3 px-3`} placeholder=" " />
          <select name="languages" id="lang" className={`${style["select"]} ${style["input-form-area"]} mt-3 px-2 `}>
            <option value="val"> </option>
            <option value="val"> </option>
            <option value="val2"> </option>
            <option value="val2"> </option>
            <option value="val2"> </option>
          </select>

          <Multiselect
            //
            className="mt-2 text-end"
            placeholder=" "
            options={options}
            onSelect={onSelect}
            onRemove={onRemove}
            displayValue="name"
            style={{ color: "red" }}
          />
          <select name="brand" id="brand" className={`${style["select"]} ${style["input-form-area"]} mt-3 px-2 `}>
            <option value="val"></option>
            <option value="val2">  </option>
            <option value="val2">  </option>
            <option value="val2"> </option>
          </select>
          <div className="text-form mt-3 ">   </div>
          <div className="mt-1 d-flex">
            <div className={`${style["color"]} ms-2 border  mt-1`} style={{ backgroundColor: "#E52C2C" }}></div>
            <div className={`${style["color"]} ms-2 border  mt-1`} style={{ backgroundColor: "white" }}></div>
            <div className={`${style["color"]} ms-2 border  mt-1`} style={{ backgroundColor: "black" }}></div>
            <img src={add} alt="" width="30px" height="35px" className="" />
          </div>
        </Col>
      </Row>
      <Row>
        <Col sm="8" className="d-flex justify-content-end ">
          <button className="btn-save d-inline mt-2 "> </button>
        </Col>
      </Row>
    </div>
  );
};

export default AdminAddProducts;
