import React from "react";
import { Link } from "react-router-dom";
import style from "./AdminSideBar.module.css";

const AdminSideBar = () => {
  return (
    <div className={style["sidebar"]}>
      <div className="d-flex flex-column">
        <Link to="/admin/allorders" style={{ textDecoration: "none" }}>
          <div className={`${style["admin-side-text"]} mt-3 border-bottom p-2 mx-auto text-center`}> </div>
        </Link>
        <Link to="/admin/allproducts" style={{ textDecoration: "none" }}>
          <div className={`${style["admin-side-text"]} my-1 border-bottom p-2 mx-auto text-center`}> </div>
        </Link>
        <Link to="/admin/addbrand" style={{ textDecoration: "none" }}>
          <div className={`${style["admin-side-text"]} my-1 border-bottom p-2 mx-auto text-center`}> </div>
        </Link>

        <Link to="/admin/addcategory" style={{ textDecoration: "none" }}>
          <div className={`${style["admin-side-text"]} my-1 border-bottom p-2 mx-auto text-center`}> </div>
        </Link>

        <Link to="/admin/addsubcategory" style={{ textDecoration: "none" }}>
          <div className={`${style["admin-side-text"]} my-1 border-bottom p-2 mx-auto text-center`}>  </div>
        </Link>
        <Link to="/admin/addproduct" style={{ textDecoration: "none" }}>
          <div className={`${style["admin-side-text"]} my-1 border-bottom p-2 mx-auto text-center`}> </div>
        </Link>
      </div>
    </div>
  );
};

export default AdminSideBar;
