import { useState } from "react";
import { useDispatch } from "react-redux";
import { useEffect } from "react";
import { getAllCategory } from "./../../Redux/actions/categoryAction";
import { useSelector } from "react-redux";
import notify from "./../useNotify";
import { createNewSubCategory } from "./../../Redux/actions/subCategoryAction";

const AddSubCategoryHook = () => {
  const [id, setId] = useState("0");
  const [name, setName] = useState("");
  const [loading, setLoading] = useState(true);

  const dispatch = useDispatch();

  useEffect(() => {
    if (!navigator.onLine) {
      notify("    ", "warn");
      return;
    }

    dispatch(getAllCategory());
  }, [dispatch]);

  // get last category state from redux
  const categories = useSelector((state) => state.allCategory.category);

  // get last subcategory state from redux
  const res = useSelector((state) => state.subCategory.subcategory);

  // on change dropdown state
  const handelChange = (e) => {
    setId(e.target.value);
  };

  // set name
  const onCahngeName = (e) => {
    e.persist();
    setName(e.target.value);
  };

  // on save data
  const handelSubmit = async (e) => {
    e.preventDefault();

    if (!navigator.onLine) {
      notify("    ", "warn");
      return;
    }

    if (id === "0") {
      notify("    ", "warn");

      return;
    }

    if (name === "") {
      notify("    ", "warn");
      return;
    }

    setLoading(true);

    await dispatch(createNewSubCategory({ name, category: id }));

    setLoading(false);
  };

  useEffect(() => {
    if (!loading) {
      if (res) {
        if (res.status === 201) {
          setName("");
          setId("0");
          notify("  ", "success");
        } else if (res === "error AxiosError: Request failed with status code 400") {
          notify("    .     ", "error");
        }
      } else {
        notify("    ", "error");
      }
      setLoading(true);
    }
  }, [loading, res]);

  return [id, name, loading, categories, res, handelSubmit, handelChange, onCahngeName];
};

export default AddSubCategoryHook;
